.header {
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
  top: 50px;
  width: 85%;
  z-index: 30;

  @include mobile {
    top: 30px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include small-mobile {
      justify-content: flex-end;
      flex-wrap: wrap;
    }
  }

  &__logo {
    font-weight: bold;
    font-size: 22px;
    line-height: 24px;
    text-transform: uppercase;
    text-shadow: 0px 0px 4px rgba(255, 210, 210, 0.5);
    padding-right: 15px;

    @include mobile {
      font-size: 14px;
      line-height: 16px;
    }

    @include small-mobile {
      margin-bottom: 15px;
    }
  }

  &__right {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-contacts {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__order {
      margin-right: 35px;
      padding: 15px 30px;
      font-weight: normal;
      font-size: 16px;
      line-height: 1.22rem;
      text-transform: uppercase;
      transition: all 0.4s;

      &:focus,
      &:hover {
        background: rgba($mainColor, 0.1);
      }

      @include big-tablet {
        margin-right: 20px;
      }

      @include tablet {
        font-size: 16px;
        padding: 12px 20px;
      }

      @include mobile {
        font-size: 14px;
        margin-right: 10px;
      }

      @media (max-width: 410px) {
        font-size: 10px;
        padding: 10px 15px;
        margin-right: 10px;
      }
    }

    &__phones {
      display: flex;
      align-items: center;
      margin-right: 40px;

      @include big-tablet {
        margin-right: 30px;
      }

      @include tablet {
        flex-direction: column;
      }

      @media (max-width: 440px) {
        margin-right: 20px;
      }
    }

    &__phone {
      display: block;
      text-transform: lowercase;
      font-size: 16px;
      line-height: 1.22rem;
      cursor: pointer;

      @include small-mobile {
        font-size: 14px;
      }

      &:first-child {
        margin-right: 20px;

        @include big-tablet {
          margin-right: 15px;
        }

        @include tablet {
          margin-right: 0;
        }
      }
    }
  }
}

.menu__body {
  position: absolute;
  width: 100vw;
  height: 110vh;
  overflow: auto;
  right: -100%;
  top: -100px;
  background-color: $blueColor;
  flex-direction: column;
  justify-content: center;
  transition: all .6s ease;
  align-items: center;
  display: flex;
  margin: 0 auto;
  padding: 100px 50px;
  text-align: center;
  opacity: 0;
  visibility: hidden;

  @include mobile {
    padding: 50px 30px;
  }

  .menu__item {
    font-size: 36px;
    line-height: 40px;
    font-family: inherit;
    cursor: pointer;
    transition: all .8s ease-in;
    &:focus, &:hover {
      text-decoration: underline;
    }
    &:not(:last-child) {
      margin-bottom: 40px;
      @include mobile {
        margin-bottom: 20px;
      }
    }
    
  @include big-tablet {
    font-size: 30px;
    line-height: 34px;
  }
  @include mobile {
    font-size: 20px;
    line-height: 24px;
  }
  }
}

.menu__body._active {
  right: -10%;
  z-index: 40;
  opacity: 1;
  visibility: visible;
}

.icon-menu {
  z-index: 100;
}
