.offer {
  padding: 80px 0 0;

  @include mobile {
    padding: 60px 0 0;
  }

  @include small-mobile {
    padding: 50px 0 0;
  }

  &__container {
    overflow: hidden;
  }

  &__title {
    font-weight: 600;
    font-size: 36px;
    line-height: 40px;

    @include mobile {
      font-size: 24px;
      line-height: 28px;
    }

    span {
      color: $whiteColor;
    }
  }

  &-item {
    max-width: 350px;
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    margin-top: 60px !important;
    margin-right: 50px !important;
    margin-left: 50px !important;

    @include mobile {
      max-width: 100%;
    }

    &__img {
      border-radius: 25px;
      max-width: 350px;
      max-height: 300px;
      width: 100%;
      height: 100%;
      object-fit: cover;
      margin: 0 auto 20px;
    }

    &__cost {
      display: block;
      color: $mainColor;
      font-weight: 600;
      font-size: 26px;
      line-height: 30px;
      margin-bottom: 20px;
      text-align: center;

      @include small-mobile {
        font-size: 18px;
        line-height: 22px;
      }
    }

    &__links {
      display: flex;
      align-items: center;
      max-width: 300px;
      text-align: center;
      margin: 0 auto;
      justify-content: space-between;
      gap: 15px;

      @include mobile {
        gap: 10px;
      }
    }

    &__link {
      font-weight: 600;
      font-size: 15px;
      line-height: 18px;
      color: rgba($whiteColor, 0.6);
      transition: all 0.4s;
      &:focus, &:hover {
        color: rgba($whiteColor, 0.9);
      }

      @include small-mobile {
        font-size: 14px;
        line-height: 18px;
      }
    }
  }

  &__slider {
    margin-bottom: 70px;

    @include mobile {
      margin-bottom: 40px;
    }
  }

  .swiper-wrapper {
    display: flex;
    flex-direction: row;
    align-items: first baseline;
    margin: 0 auto;
  }

  .swiper-pagination-bullets {
    margin: 70px auto 0;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

    @include mobile {
      margin: 40px auto 0;
    }
  }

  .swiper-pagination-bullet {
    margin: 0 auto;
    background: none;
    width: 14px;
    height: 28px;
    text-align: center;
    font-size: 24px;
    line-height: 28px;
    color: rgba($whiteColor, 0.6);
    transition: color 0.3s fade-in;

    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  .swiper-pagination-bullet-active {
    color: #fff;
  }
}
